.gpt3__navbar{
    display: flex;
    justify-content:space-between;
    align-items: center;
    padding: 2rem 4rem;
}

.gpt3__navbar-links_logo{
    margin-right: 2rem;
}


.gpt3__navbar-links_logo img{
    width: 62.56px;
    height: 16.02px;
}

.gpt3__navbar-links{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content:flex-start;
}

.gpt3__navbar-links_container{
    display: flex;
}

.gpt3__navbar-credentials{
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.gpt3__navbar-menu{
    margin-left: 1rem;
    display: none;
    position: relative;
    
}

.gpt3__navbar-menu svg{
    cursor: pointer;
}

.gpt3__navbar-menu_container{
    background: var(--color-footer);
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-end;
    text-align: end;
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);
}

.gpt3__navbar-menu_container p{
    margin: 1rem 0;
}

.gpt3__navbar-menu_container-links-credentials{
    display: none;
}

.gpt3__navbar-links_container p,
.gpt3__navbar-credentials p,
.gpt3__navbar-menu_container p {
    color: white;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    text-transform: capitalize;
    margin: 0 1rem;
    cursor: pointer;
    line-height: 25px;
}

.gpt3__navbar-credentials button,
.gpt3__navbar-menu_container button{
    color: white;
    font-family: var(--font-family);
    background: #FF4820;
    font-size: 18px;
    line-height: 25px;
    font-weight: 400;
    padding: .5rem 1.5rem;
    border-radius: 5px;
    border: 0;
    outline: none;
    cursor: pointer;
}



@media screen and (max-width: 1080px){
    .gpt3__navbar-links_container{
        display: none;
    }

    .gpt3__navbar-menu{
        display: flex;
    } 
}

@media screen and (max-width: 720px){
    .gpt3__navbar{
        padding: 1.5rem 2rem;
    }
}

@media screen and (max-width: 540px){
    .gpt3__navbar{
        padding: 2rem;
    }

    .gpt3__navbar-credentials{
        display: none;
    }

    .gpt3__navbar-menu_container-links-credentials{
        display: block;
    }
}